    width: calc(20% + 100px) -  to calculate some related size to its parent
*/
@charset "utf-8";
:root {
    --main-bg-color-one: #242428;
    --main-bg-color-two: #ffb416;
}
*, 
*:before, 
*:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; 
}
body { margin:0; }
img { border:0; }
.color-title { color:var(--main-bg-color-two); } 
.phone-arrow-down, 
.phone-arrow-up {
    display:none;
}
.tab-arrow-down { transform: rotate(270deg); display: inline-block; margin: 0 30px; }

.nav_link {
    text-decoration: none;
    color: white;
    font-weight: bolder;
    display: inline-block;
    padding: 15px;
    transition: ease-out 700ms;
}
.nav_link:hover {
    background-color: var(--main-bg-color-two);
    color: var(--main-bg-color-one);
}
.nav_link:active {
    color: var(--main-bg-color-two);
    text-decoration: underline;
}
h4 { margin:0; }
h4 button {
    text-transform: uppercase;
    font-weight: bolder;
    border: 0;
    border-left: 5px solid var(--main-bg-color-one);
    margin: 0;
    font-size: 1vw;
    width: 100%;
}
ul {
    list-style: none;
    padding: 0;
    margin-top: 5px;
    margin-bottom:0;
}
li {
    padding: 5px;
    padding-left: 0;
    margin-left: 0;
    font-size: .97vw;
}

.Nav { margin:auto; width:100%;background-color:#36486b; position:fixed; top:0; z-index:1; }
.Nav > section{ display:inline-block; }
.nav_left { width:30%; }
.nav_left h1 { text-align:center; color:white; }
.nav_right { width:70%; text-align:right; }

aside {
    position: fixed;
    top:59px;
    width: 19%;
    height: 100vh;
    overflow: scroll;
    background-color:#f9f9f9;
}
aside section:first-child { text-align:center; }
aside section:first-child button { margin-top:10px; }
aside section:first-child h2 {
    margin: 0;
    font-size: 1em;
    text-transform: uppercase;
}
aside section:first-child img{ width:50px; padding:.5rem;}
aside section { padding:15px; padding-top:0; }

.main {
    margin-top: 75px;
    width: 81%;
    left: 19%;
    position: relative;
}


@media screen and (max-width:800px), screen and (max-width:1000px) and (orientation:landscape) {

}
@media screen and (max-width:480px){
    .phone-arrow-up {
        transform: rotate(90deg);
        display: inline-block;
        margin: 0 30px;
    }
    .phone-arrow-down {
        transform: rotate(270deg);
        display: inline-block;
        margin: 0 30px;
    }
    .Nav { height:100px;overflow:scroll; }
    .Nav > section{ display:block; }
    .nav_link { display:block; text-align:center; }
    .nav_left { background-color:var(--main-bg-color-one); z-index:1; }
    .nav_left h1 { margin:13px auto; }
    .nav_left,
    .nav_right {
        width: 100%;
        height: 50px;
        border: 1px solid transparent;
    }
    .nav_left { position:fixed; top:0;}
    .nav_right { margin-top:50px; }

}